<template>
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="学生学号" prop="name">
    <el-input  v-model="sno" ></el-input>
  </el-form-item>
 <el-form-item label="课程名称" prop="region">
    <el-select placeholder="请选择课程名称" v-model="course">
      <el-option v-for="item in courses" :key="item[0]" :label="item[1]" :value="item[1]"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="课程分数" prop="name">
    <el-input v-model="score"></el-input>
  </el-form-item>
  <el-form-item label="开课学期" prop="region">
    <el-select placeholder="请选择开课学期" v-model="select">
      <el-option label="2024-2025春季学期" value="2024-2025春季学期"></el-option>
      <el-option label="2024-2025秋季学期" value="2024-2025秋季学期"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="create">立即创建</el-button>
    <el-button @click="clear">重置</el-button>
  </el-form-item>
</el-form>  
</template>
<script>
  export default {
    data() {
      return {
        course:'',
        select:null,
        courses: [], // 用于存储课程数据
        sno:'',
        score:''
      };
    },
    methods: {
      create(){
      let params = new URLSearchParams();
      params.append('sno', this.sno);
      params.append('courseid', this.course);
      params.append('score', this.score);
      params.append('term', this.select);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8689/addscore",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message=="successfully"){
            this.$message({
          type: 'success',
          message: '操作成功!'
        });
          this.sno = '';
          this.course = '';
          this.score = '';
          this.select = null;
        }else{
          this.$message({
          type: 'warning',
          message: '操作失败!'
        });
        }
      }).catch((error) => {
        console.error('请求失败:', error);
      });
      },
      clear() {
      this.sno = '';
      this.course = '';
      this.score = '';
      this.select = null;
}
    },
    mounted(){
      let params = new URLSearchParams();
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8689/coursenum",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.courses = result.data; // 将获取到的课程数据存储到courses数组中
      }).catch((error) => {
        console.error('请求失败:', error);
      });
    }
  }
</script>